import { ProDescriptions } from "@ant-design/pro-components";
import { Card, Row, Col, Divider, Typography, Button } from "antd";
import TitleHeader from "@Components/TitleHeader/TitleHeader";
import { useNavigate } from "react-router-dom";
import { useEffect, useState } from "React";
import { useCart } from "@Utils/Cart/Cart";

const App = () => {
  const navigate = useNavigate();
  const cart = useCart();
  const [product, setProduct] = useState(0);
  const [shippingFee, setShippingFee] = useState(0);
  const [total, setTotal] = useState(0);

  useEffect(() => {
    if (cart.current.length <= 0) {
      navigate(`/cart`);
    }
    var order = JSON.parse(localStorage.getItem("order"));
    setProduct(order.product);
    setShippingFee(order.shippingFee);
    setTotal(order.total);
  }, []);

  return (
    <>
      <div style={{ position: "fixed", top: 0, width: "100%", zIndex: 16 }}>
        <TitleHeader title={"付款方法"} back={"/cart/shipping"} />
      </div>

      <div style={{ padding: "155px 30px 30px 30px", height: "100%" }}>
        <Card
          title={"匯款"}
          block={true}
          style={{
            borderColor: "#dba59f",
          }}
        >
          <ProDescriptions column={1} bordered={true}>
            <ProDescriptions.Item label="銀行代號" valueType="text">
              006 (合作金庫銀行)
            </ProDescriptions.Item>
            <ProDescriptions.Item label="帳號" valueType="text">
              0650717237085
            </ProDescriptions.Item>
            <ProDescriptions.Item label="戶名" valueType="text">
              石上企業社
            </ProDescriptions.Item>
          </ProDescriptions>
        </Card>

        <div
          style={{
            padding: "10px",
          }}
        >
          <Row>
            <Col offset={0} span={24}>
              <Divider orientation="right">
                <Typography.Text>共 {0} 件商品</Typography.Text>
              </Divider>

              <ProDescriptions column={1} bordered={true}>
                <ProDescriptions.Item label="商品金額" valueType="text">
                  ${product}
                </ProDescriptions.Item>
                <ProDescriptions.Item label="活動折抵" valueType="text">
                  $0
                </ProDescriptions.Item>
                <ProDescriptions.Item label="運費" valueType="text">
                  ${shippingFee}
                </ProDescriptions.Item>
                <ProDescriptions.Item label="總計" valueType="text">
                  ${total}
                </ProDescriptions.Item>
              </ProDescriptions>
            </Col>
          </Row>
          <Button
            type="primary"
            size={"large"}
            block={true}
            style={{ marginTop: 20 }}
            onClick={() => {
              navigate(`/cart/payment`);
            }}
          >
            我已匯款，上傳明細
          </Button>
          <Button
            size={"large"}
            block={true}
            style={{ marginTop: 20, borderColor: "#dba59f", color: "#dba59f" }}
            onClick={() => {}}
          >
            取消訂單
          </Button>
        </div>
      </div>
    </>
  );
};

export default App;
